import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { Button } from 'react-bootstrap';
import Liberator from './components/Liberator';

require('./styles/main.css');

const texts = [
        'This is the liberator.',
        'It liberates the component from captivity of its siblings.',
        'This is the foundation for popups.'
    ];

export class App extends Component {

    constructor(props) {
        super(props);

        this.state = {
            visible: false,
            text: texts[0]
        };

        this.onShowButtonClick = this.onShowButtonClick.bind(this);
        this.onChangeButtonClick = this.onChangeButtonClick.bind(this);

        this.count = 0;
    }

    onShowButtonClick() {
        this.setState({
            visible: !this.state.visible
        });
    }

    onChangeButtonClick() {
        this.count ++;
        this.setState({
            text: texts[this.count % texts.length]
        });
    }

    render() {
        var visible = this.state.visible,
            popup = visible ? (
            <Liberator>
                <div className='popup red'>{this.state.text}</div>
            </Liberator>
        ) : null;

        return (
            <div>
                <Button bsStyle="primary" bsSize="large" onClick={this.onShowButtonClick}>{visible ? 'Hide popup' : 'Show popup'}</Button>
                <Button bsSize="large" className="indent" onClick={this.onChangeButtonClick}>Change popup text</Button>
                {popup}
            </div>
        );
    }
}


